// src/components/Login.jsx - TEST VERSION with obvious changes
// This is your app's front door - where students and teachers enter

import React, { useState } from 'react';
import { loginStudent, loginTeacher } from '../services/auth';

const Login = ({ onLoginSuccess }) => {
  // State to track form data
  const [loginType, setLoginType] = useState('student'); // 'student' or 'teacher'
  const [formData, setFormData] = useState({
    username: '',
    email: '',
    password: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  // Handle input changes
  const handleInputChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
    // Clear error when user starts typing
    if (error) setError('');
  };

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');

    try {
      let result;
      
      if (loginType === 'student') {
        // Student login with username/password
        result = await loginStudent(formData.username, formData.password);
      } else {
        // Teacher login with email/password
        result = await loginTeacher(formData.email, formData.password);
      }

      if (result.success) {
        console.log(' Login successful!', result.user);
        onLoginSuccess(result.user); // Tell parent component user logged in
      } else {
        setError(result.error);
      }
    } catch (err) {
      setError('Something went wrong. Please try again.');
      console.error('Login error:', err);
    }

    setLoading(false);
  };

  return (
    <div 
      className="min-h-screen flex items-center justify-center px-4"
      style={{ backgroundColor: '#ecf0f1' }} // Branco Titnio inline para testar
    >
      <div className="w-full max-w-md">
        {/* Header */}
        <div className="text-center mb-8">
          <div className="text-4xl mb-2"></div>
          <h1 
            className="text-3xl font-bold mb-2"
            style={{ color: '#2c3e50' }} // Cinza Escuro inline
          >
            Teacher Alex's English Academy
          </h1>
          <p style={{ color: '#7f8c8d' }}>Welcome back! Please sign in to continue.</p>
        </div>

        {/* Login Card */}
        <div className="bg-white rounded-2xl shadow-xl p-8">
          
          {/* Login Type Selector */}
          <div className="flex mb-6">
            <button
              type="button"
              onClick={() => setLoginType('student')}
              className={`flex-1 py-3 px-4 rounded-l-lg font-medium transition-all ${
                loginType === 'student'
                  ? 'text-white shadow-md'
                  : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
              }`}
              style={loginType === 'student' ? { backgroundColor: '#196de3' } : {}}
            >
               Student
            </button>
            <button
              type="button"
              onClick={() => setLoginType('teacher')}
              className={`flex-1 py-3 px-4 rounded-r-lg font-medium transition-all ${
                loginType === 'teacher'
                  ? 'text-white shadow-md'
                  : 'bg-gray-100 text-gray-600 hover:bg-gray-200'
              }`}
              style={loginType === 'teacher' ? { backgroundColor: '#bf3d30' } : {}} // Vermelho Elegante, no verde
            >
               Teacher
            </button>
          </div>

          {/* Login Form */}
          <form onSubmit={handleSubmit} className="space-y-4">
            
            {/* Student Login Fields */}
            {loginType === 'student' && (
              <div>
                <label 
                  className="block text-sm font-medium mb-2"
                  style={{ color: '#2c3e50' }}
                >
                  Username
                </label>
                <input
                  type="text"
                  name="username"
                  value={formData.username}
                  onChange={handleInputChange}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:border-transparent"
                  style={{ '--tw-ring-color': '#196de3' }} // Azul Moderno para student
                  placeholder="Enter your username"
                  required
                />
              </div>
            )}

            {/* Teacher Login Fields */}
            {loginType === 'teacher' && (
              <div>
                <label 
                  className="block text-sm font-medium mb-2"
                  style={{ color: '#2c3e50' }}
                >
                  Email
                </label>
                <input
                  type="email"
                  name="email"
                  value={formData.email}
                  onChange={handleInputChange}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:border-transparent"
                  style={{ '--tw-ring-color': '#bf3d30' }} // Vermelho Elegante para teacher
                  placeholder="Enter your email"
                  required
                />
              </div>
            )}

            {/* Password Field (for both) */}
            <div>
              <label 
                className="block text-sm font-medium mb-2"
                style={{ color: '#2c3e50' }}
              >
                Password
              </label>
              <input
                type="password"
                name="password"
                value={formData.password}
                onChange={handleInputChange}
                className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:border-transparent"
                placeholder="Enter your password"
                required
              />
            </div>

            {/* Error Message */}
            {error && (
              <div className="bg-red-50 border border-red-200 rounded-lg p-3">
                <p style={{ color: '#bf3d30' }} className="text-sm"> {error}</p>
              </div>
            )}

            {/* Submit Button - OBVIOUS CHANGE */}
            <button
              type="submit"
              disabled={loading}
              className={`w-full py-3 px-4 rounded-lg font-medium text-white transition-all transform hover:scale-105 ${
                loading ? 'opacity-50 cursor-not-allowed' : ''
              }`}
              style={{ 
                backgroundColor: loginType === 'student' ? '#196de3' : '#bf3d30', // Azul para student, Vermelho para teacher
                filter: 'brightness(1)'
              }}
              onMouseEnter={(e) => e.target.style.filter = 'brightness(0.9)'}
              onMouseLeave={(e) => e.target.style.filter = 'brightness(1)'}
            >
              {loading ? (
                <div className="flex items-center justify-center">
                  <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white mr-2"></div>
                  Signing in...
                </div>
              ) : (
                `Sign in as ${loginType === 'student' ? 'Student' : 'Teacher'}`
              )}
            </button>

          </form>

          {/* Demo Credentials (for testing) */}
          <div className="mt-6 p-4 bg-gray-50 rounded-lg">
            <p className="text-xs text-center" style={{ color: '#7f8c8d' }}>
               Demo Mode - You'll create real accounts from teacher dashboard
            </p>
          </div>

        </div>

        {/* Footer */}
        <div className="text-center mt-6">
          <p className="text-sm" style={{ color: '#7f8c8d' }}>
            Made with  for Brazilian English learners 
          </p>
        </div>
      </div>
    </div>
  );
};

export default Login;