// src/App.js - CLEAN PROFESSIONAL PALETTE

import React, { useState, useEffect } from 'react';
import { onAuthChange } from './services/auth';
import Login from './components/Login';
import TeacherDashboard from './components/TeacherDashboard';
import StudentPortal from './components/student/StudentPortal';

function App() {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  // Listen for authentication changes
  useEffect(() => {
    const unsubscribe = onAuthChange((authUser) => {
      if (authUser) {
        console.log(' User is logged in:', authUser);
        // We'll get full user data from the login function
      } else {
        console.log(' User is logged out');
        setUser(null);
      }
      setLoading(false);
    });

    return () => unsubscribe(); // Cleanup listener
  }, []);

  // Handle successful login
  const handleLoginSuccess = (userData) => {
    console.log(' Login successful in App:', userData);
    setUser(userData);
  };

  // Handle logout
  const handleLogout = async () => {
    const { logout } = await import('./services/auth');
    await logout();
    setUser(null);
  };

  // Show loading while checking auth status
  if (loading) {
    return (
      <div 
        className="min-h-screen flex items-center justify-center"
        style={{ backgroundColor: '#ecf0f1' }} // Branco Titnio
      >
        <div className="text-center">
          <div 
            className="animate-spin rounded-full h-12 w-12 border-b-2 mx-auto mb-4"
            style={{ borderBottomColor: '#196de3' }} // Azul Moderno
          ></div>
          <p style={{ color: '#7f8c8d' }}>Loading Teacher Alex's Academy...</p>
        </div>
      </div>
    );
  }

  // Show login if no user
  if (!user) {
    return <Login onLoginSuccess={handleLoginSuccess} />;
  }

  // Show different dashboards based on user role
  if (user.role === 'teacher') {
    return <TeacherDashboard user={user} onLogout={handleLogout} />;
  }

  // Show Student Portal
  if (user.role === 'student') {
    return <StudentPortal user={user} onLogout={handleLogout} />;
  }

  // Fallback
  return (
    <div 
      className="min-h-screen flex items-center justify-center"
      style={{ backgroundColor: '#ecf0f1' }}
    >
      <div className="text-center">
        <h2 
          className="text-xl font-bold mb-4"
          style={{ color: '#2c3e50' }}
        >
          Unknown user role
        </h2>
        <button
          onClick={handleLogout}
          className="text-white px-4 py-2 rounded-lg transition-colors"
          style={{ backgroundColor: '#bf3d30' }}
          onMouseEnter={(e) => e.target.style.filter = 'brightness(0.9)'}
          onMouseLeave={(e) => e.target.style.filter = 'brightness(1)'}
        >
          Logout
        </button>
      </div>
    </div>
  );
}

export default App;