// Calculate level from total XP
export const calculateLevel = (totalXP) => {
  for (let i = LEVEL_THRESHOLDS.length - 1; i >= 0; i--) {
    if (totalXP >= LEVEL_THRESHOLDS[i]) {
      return i + 1;
    }
  }
  return 1;
};

// Calculate XP needed for next level
export const getXPToNextLevel = (totalXP) => {
  const currentLevel = calculateLevel(totalXP);
  if (currentLevel >= LEVEL_THRESHOLDS.length) {
    return 0; // Max level reached
  }
  
  return LEVEL_THRESHOLDS[currentLevel] - totalXP;
};

// Calculate XP progress within current level
export const getLevelProgress = (totalXP) => {
  const currentLevel = calculateLevel(totalXP);
  if (currentLevel >= LEVEL_THRESHOLDS.length) {
    return 100; // Max level
  }
  
  const currentLevelXP = LEVEL_THRESHOLDS[currentLevel - 1];
  const nextLevelXP = LEVEL_THRESHOLDS[currentLevel];
  const progressXP = totalXP - currentLevelXP;
  const neededXP = nextLevelXP - currentLevelXP;
  
  return Math.round((progressXP / neededXP) * 100);
};

// Award XP for different activities
export const awardXP = async (username, activityType, data = {}) => {
  try {
    console.log(` Awarding XP: ${activityType} for ${username}`, data);
    
    let xpEarned = 0;
    
    // Calculate XP based on activity type
    switch (activityType) {
      case 'LESSON_COMPLETE':
        xpEarned = XP_REWARDS.LESSON_BASE + 
                   (data.correctAnswers * XP_REWARDS.LESSON_CORRECT_ANSWER);
        
        // Perfect score bonus
        if (data.perfectScore || data.correctAnswers === data.totalQuestions) {
          xpEarned += XP_REWARDS.LESSON_PERFECT_BONUS;
        }
        break;
        
      case 'DRAG_DROP_COMPLETE':
        xpEarned = XP_REWARDS.DRAG_DROP_COMPLETE * (data.difficulty || 1);
        break;
        
      case 'SPEAKING_EXERCISE':
        xpEarned = XP_REWARDS.SPEAKING_EXERCISE;
        if (data.accuracy >= 90) {
          xpEarned += 20; // Bonus for high accuracy
        }
        break;
        
      case 'DAILY_STREAK':
        xpEarned = XP_REWARDS.DAILY_STREAK * (data.streakDay || 1);
        break;
        
      case 'WEEKLY_PERFECT':
        xpEarned = XP_REWARDS.WEEKLY_PERFECT;
        break;
        
      case 'ACHIEVEMENT_UNLOCK':
        xpEarned = XP_REWARDS.ACHIEVEMENT_UNLOCK;
        break;
        
      default:
        console.warn(`Unknown activity type: ${activityType}`);
        return 0;
    }
    
    // Get current student data
    const studentRef = doc(db, 'students', username);
    const studentDoc = await getDoc(studentRef);
    
    if (!studentDoc.exists()) {
      throw new Error('Student not found');
    }
    
    const currentData = studentDoc.data();
    const oldTotalXP = currentData.totalXP || 0;
    const newTotalXP = oldTotalXP + xpEarned;
    
    // Calculate level changes
    const oldLevel = calculateLevel(oldTotalXP);
    const newLevel = calculateLevel(newTotalXP);
    const leveledUp = newLevel > oldLevel;
    
    // Create XP history entry
    const xpEntry = {
      type: activityType,
      xpEarned: xpEarned,
      timestamp: new Date(),
      data: data
    };
    
    // Update student document
    const updateData = {
      totalXP: newTotalXP,
      level: newLevel,
      lastActive: new Date()
    };
    
    // Add XP history entry
    const xpHistory = currentData.xpHistory || [];
    xpHistory.push(xpEntry);
    
    // Keep only last 100 entries to avoid bloat
    if (xpHistory.length > 100) {
      xpHistory.splice(0, xpHistory.length - 100);
    }
    updateData.xpHistory = xpHistory;
    
    // Update monster evolution stage based on level
    updateData.monsterStage = getMonsterStage(newLevel);
    
    await updateDoc(studentRef, updateData);
    
    console.log(` XP awarded successfully!`, {
      username,
      activityType,
      xpEarned,
      oldLevel,
      newLevel,
      leveledUp,
      totalXP: newTotalXP
    });
    
    return {
      xpEarned,
      totalXP: newTotalXP,
      level: newLevel,
      leveledUp,
      monsterStage: updateData.monsterStage
    };
    
  } catch (error) {
    console.error(' Failed to award XP:', error);
    throw error;
  }
};

// Get monster evolution stage based on level
export const getMonsterStage = (level) => {
  if (level >= 40) return 'legend';      // Level 40+ = Legend
  if (level >= 30) return 'master';      // Level 30+ = Master  
  if (level >= 20) return 'adult';       // Level 20+ = Adult
  if (level >= 10) return 'teen';        // Level 10+ = Teen
  if (level >= 5) return 'child';        // Level 5+ = Child
  return 'baby';                         // Level 1-4 = Baby
};

// Get student's XP data
export const getStudentXPData = async (username) => {
  try {
    const studentRef = doc(db, 'students', username);
    const studentDoc = await getDoc(studentRef);
    
    if (!studentDoc.exists()) {
      return {
        totalXP: 0,
        level: 1,
        xpToNextLevel: 50,
        levelProgress: 0,
        monsterStage: 'baby',
        xpHistory: []
      };
    }
    
    const data = studentDoc.data();
    const totalXP = data.totalXP || 0;
    const level = calculateLevel(totalXP);
    
    return {
      totalXP,
      level,
      xpToNextLevel: getXPToNextLevel(totalXP),
      levelProgress: getLevelProgress(totalXP),
      monsterStage: getMonsterStage(level),
      xpHistory: data.xpHistory || []
    };
    
  } catch (error) {
    console.error(' Failed to get student XP data:', error);
    throw error;
  }
};

// Reset student XP (for testing/admin)
export const resetStudentXP = async (username) => {
  try {
    const studentRef = doc(db, 'students', username);
    await updateDoc(studentRef, {
      totalXP: 0,
      level: 1,
      monsterStage: 'baby',
      xpHistory: []
    });
    
    console.log(` Reset XP for ${username}`);
    return true;
    
  } catch (error) {
    console.error(' Failed to reset XP:', error);
    throw error;
  }
};