// src/components/student/views/SeriesSelector.jsx
// Series selection with glassmorphism elegance - COOL-PROFESSIONAL SYSTEM

import React, { useState, useEffect } from 'react';
import { getStudentData } from '../../../services/database.js';

const SeriesSelector = ({ user, onBack, onSeriesSelect }) => {
  const [seriesProgress, setSeriesProgress] = useState({});
  const [loading, setLoading] = useState(true);

  // Define all 9 lesson series with metadata - COOL-PROFESSIONAL COLORS
  const lessonSeries = [
    {
      id: 'foundation',
      title: 'Foundation',
      description: 'Domine os fundamentos do ingls',
      color: '#196de3',
      totalLessons: 10,
      icon: 'foundation'
    },
    {
      id: 'sports',
      title: 'Sports',
      description: 'Futebol, Olimpadas e paixo brasileira',
      color: '#27ae60',
      totalLessons: 10,
      icon: 'sports'
    },
    {
      id: 'culture',
      title: 'Culture',
      description: 'Tradies brasileiras e cultura global',
      color: '#9b59b6',
      totalLessons: 10,
      icon: 'culture'
    },
    {
      id: 'travel',
      title: 'Travel',
      description: 'Explore o mundo com ingls',
      color: '#3498db',
      totalLessons: 10,
      icon: 'travel'
    },
    {
      id: 'food',
      title: 'Food',
      description: 'Do aa  culinria internacional',
      color: '#e67e22',
      totalLessons: 10,
      icon: 'food'
    },
    {
      id: 'movies',
      title: 'Movies',
      description: 'Cinema, entretenimento e narrativas',
      color: '#bf3d30',
      totalLessons: 10,
      icon: 'movies'
    },
    {
      id: 'gaming',
      title: 'Gaming',
      description: 'Videogames e entretenimento digital',
      color: '#34495e',
      totalLessons: 10,
      icon: 'gaming'
    },
    {
      id: 'business',
      title: 'Business',
      description: 'Ingls profissional e corporativo',
      color: '#2c5282',
      totalLessons: 10,
      icon: 'business'
    },
    {
      id: 'portal',
      title: 'Portal',
      description: 'Tpicos avanados e contedo especial',
      color: '#f39c12',
      totalLessons: 10,
      icon: 'portal',
      comingSoon: true
    }
  ];

  // Better series icons - no more stars!
  const SeriesIcon = ({ type, className = "w-8 h-8" }) => {
    const iconProps = { className: `${className} text-white`, fill: "currentColor", viewBox: "0 0 24 24" };
    
    switch(type) {
      case 'foundation':
        return (
          <svg {...iconProps}>
            <path d="M12 1L3 5v6c0 5.55 3.84 10.74 9 12 5.16-1.26 9-6.45 9-12V5l-9-4z"/>
          </svg>
        );
      case 'sports':
        return (
          <svg {...iconProps}>
            <path d="M7 4V2C7 1.45 7.45 1 8 1s1 .45 1 1v2h6V2c0-.55.45-1 1-1s1 .45 1 1v2h1c1.1 0 2 .9 2 2v14c0 1.1-.9 2-2 2H6c-1.1 0-2-.9-2-2V6c0-1.1.9-2 2-2h1zm11 6H6v10h12V10zm-9 7l-1.41-1.41L6 17.17l3.59 3.59L16 14.35 14.59 13 9.5 17z"/>
          </svg>
        );
      case 'culture':
        return (
          <svg {...iconProps}>
            <path d="M12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm-2 15l-5-5 1.41-1.41L10 14.17l7.59-7.59L19 8l-9 9z"/>
          </svg>
        );
      case 'travel':
        return (
          <svg {...iconProps}>
            <path d="M14.06 9.02l.92.92L5.92 19H5v-.92l9.06-9.06M17.66 3c-.25 0-.51.1-.7.29l-1.83 1.83 3.75 3.75 1.83-1.83c.39-.39.39-1.02 0-1.41l-2.34-2.34c-.2-.2-.45-.29-.71-.29zm-3.6 3.19L3 17.25V21h3.75L17.81 9.94l-3.75-3.75z"/>
          </svg>
        );
      case 'food':
        return (
          <svg {...iconProps}>
            <path d="M8.1 13.34l2.83-2.83L3.91 3.5c-1.56 1.56-1.56 4.09 0 5.66l4.19 4.18zm6.78-1.81c1.53.71 3.68.21 5.27-1.38 1.91-1.91 2.28-4.65.81-6.12-1.46-1.46-4.20-1.10-6.12.81-1.59 1.59-2.09 3.74-1.38 5.27L3.7 19.87l1.41 1.41L12 14.41l6.88 6.88 1.41-1.41-6.88-6.88 1.27-1.27z"/>
          </svg>
        );
      case 'movies':
        return (
          <svg {...iconProps}>
            <path d="M18 4l2 4h-3l-2-4h-2l2 4h-3l-2-4H8l2 4H7L5 4H4c-1.1 0-1.99.9-1.99 2L2 18c0 1.1.9 2 2 2h16c1.1 0 2-.9 2-2V4h-4z"/>
          </svg>
        );
      case 'gaming':
        return (
          <svg {...iconProps}>
            <path d="M15 7.5V2H9v5.5l3 3 3-3zM7.5 9H2v6h5.5l3-3-3-3zM9 16.5V22h6v-5.5l-3-3-3 3zM16.5 9l-3 3 3 3H22V9h-5.5z"/>
          </svg>
        );
      case 'business':
        return (
          <svg {...iconProps}>
            <path d="M12 7V3H2v18h20V7H12zM6 19H4v-2h2v2zm0-4H4v-2h2v2zm0-4H4V9h2v2zm0-4H4V5h2v2zm4 12H8v-2h2v2zm0-4H8v-2h2v2zm0-4H8V9h2v2zm0-4H8V5h2v2zm10 12h-8v-2h2v-2h-2v-2h2v-2h-2V9h8v10z"/>
          </svg>
        );
      case 'portal':
        return (
          <svg {...iconProps}>
            <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z"/>
          </svg>
        );
      default:
        return (
          <svg {...iconProps}>
            <path d="M12 2l3.09 6.26L22 9.27l-5 4.87 1.18 6.88L12 17.77l-6.18 3.25L7 14.14 2 9.27l6.91-1.01L12 2z"/>
          </svg>
        );
    }
  };

  // Load progress data for all series
  useEffect(() => {
    const loadProgress = async () => {
      try {
        // Get student data which includes progress
        const result = await getStudentData(user.username);
        
        if (result.success && result.student && result.student.progress) {
          // Extract progress data for each series
          const progress = {};
          for (const series of lessonSeries) {
            // Skip coming soon series
            if (series.comingSoon) {
              progress[series.id] = { completedLessons: 0, totalXP: 0 };
              continue;
            }
            
            // Count completed lessons and total XP for this series
            const seriesProgress = Object.entries(result.student.progress || {})
              .filter(([lessonId]) => lessonId.startsWith(series.id))
              .reduce((acc, [lessonId, lessonData]) => {
                if (lessonData.completed) {
                  acc.completedLessons += 1;
                  acc.totalXP += lessonData.xpEarned || 0;
                }
                return acc;
              }, { completedLessons: 0, totalXP: 0 });
            
            progress[series.id] = seriesProgress;
          }
          setSeriesProgress(progress);
        } else {
          // Initialize empty progress if no data found
          const emptyProgress = {};
          lessonSeries.forEach(series => {
            emptyProgress[series.id] = { completedLessons: 0, totalXP: 0 };
          });
          setSeriesProgress(emptyProgress);
        }
      } catch (error) {
        console.error('Error loading series progress:', error);
        // Initialize empty progress if loading fails
        const emptyProgress = {};
        lessonSeries.forEach(series => {
          emptyProgress[series.id] = { completedLessons: 0, totalXP: 0 };
        });
        setSeriesProgress(emptyProgress);
      } finally {
        setLoading(false);
      }
    };

    loadProgress();
  }, [user.username]);

  // Calculate completion percentage for a series
  const getCompletionPercentage = (seriesId, totalLessons) => {
    const progress = seriesProgress[seriesId];
    if (!progress) return 0;
    return Math.round((progress.completedLessons / totalLessons) * 100);
  };

  // Handle series selection
  const handleSeriesClick = (series) => {
    if (series.comingSoon) return;
    onSeriesSelect(series.id, series.title);
  };

  // Calculate overall stats for header
  const getOverallStats = () => {
    const totalCompleted = Object.values(seriesProgress).reduce((sum, progress) => sum + progress.completedLessons, 0);
    const totalXP = Object.values(seriesProgress).reduce((sum, progress) => sum + progress.totalXP, 0);
    const seriesStarted = Object.values(seriesProgress).filter(progress => progress.completedLessons > 0).length;
    
    return { totalCompleted, totalXP, seriesStarted };
  };

  if (loading) {
    return (
      <div 
        className="min-h-screen flex items-center justify-center"
        style={{ backgroundColor: '#ecf0f1' }}
      >
        <div className="text-center">
          <div 
            className="animate-spin rounded-full h-12 w-12 border-b-2 mx-auto mb-4"
            style={{ borderBottomColor: '#3498db' }}
          ></div>
          <p style={{ color: '#7f8c8d' }}>Carregando sries...</p>
        </div>
      </div>
    );
  }

  const stats = getOverallStats();

  return (
    <>
      {/* Custom CSS - Cool-Professional System */}
      <style>{`
        .glass-card {
          background: rgba(255, 255, 255, 0.95);
          backdrop-filter: blur(10px);
          border: 1px solid rgba(255, 255, 255, 0.5);
        }
        
        .elegant-header-stats {
          background: rgba(255, 255, 255, 0.1);
          backdrop-filter: blur(10px);
          border: 1px solid rgba(255, 255, 255, 0.2);
        }
        
        .brand-dark { color: #2c3e50; }
        .brand-light-gray { color: rgba(127, 140, 141, 0.8); }
      `}</style>

      <div 
        className="min-h-screen pb-20"
        style={{ backgroundColor: '#ecf0f1' }}
      >
        {/* Cool-Professional Neutral Header */}
        <div 
          className="text-white shadow-lg"
          style={{
            background: 'linear-gradient(135deg, #2c3e50 0%, #34495e 100%)',
            boxShadow: '0 4px 20px rgba(44, 62, 80, 0.2)'
          }}
        >
          <div className="max-w-4xl mx-auto px-4 py-6">
            <div className="flex items-center justify-between mb-4">
              <button
                onClick={onBack}
                className="flex items-center text-white opacity-90 hover:opacity-100 transition-opacity"
              >
                <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
                Voltar ao Hub
              </button>
              <div className="w-6"></div>
            </div>
            
            <div className="text-center">
              <h1 
                className="text-2xl font-semibold mb-2" 
                style={{ 
                  color: 'white',
                  fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif'
                }}
              >
                Escolha sua Srie
              </h1>
              <p className="text-white opacity-90 mb-4">9 sries de lies disponveis</p>
              
              {/* Mini Stats in Header */}
              <div className="elegant-header-stats grid grid-cols-3 gap-4 rounded-xl p-4">
                <div className="text-center">
                  <div className="text-lg font-bold">{stats.totalCompleted}</div>
                  <div className="text-xs text-white opacity-80">Concludas</div>
                </div>
                <div className="text-center">
                  <div className="text-lg font-bold">{stats.totalXP}</div>
                  <div className="text-xs text-white opacity-80">XP Total</div>
                </div>
                <div className="text-center">
                  <div className="text-lg font-bold">{stats.seriesStarted}/8</div>
                  <div className="text-xs text-white opacity-80">Sries Iniciadas</div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Series Grid */}
        <div className="max-w-4xl mx-auto px-4 py-8">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
            {lessonSeries.map((series) => {
              const completionPercentage = getCompletionPercentage(series.id, series.totalLessons);
              const progress = seriesProgress[series.id] || { completedLessons: 0, totalXP: 0 };

              return (
                <div
                  key={series.id}
                  onClick={() => handleSeriesClick(series)}
                  className={`glass-card group transform transition-all duration-300 hover:scale-105 hover:shadow-2xl rounded-2xl p-8 ${
                    series.comingSoon 
                      ? 'opacity-60 cursor-not-allowed'
                      : 'cursor-pointer'
                  }`}
                  style={{
                    boxShadow: `0 8px 32px ${series.color}22`
                  }}
                >
                  {/* Icon & Title Section */}
                  <div className="flex items-center mb-6">
                    <div 
                      className={`w-16 h-16 rounded-2xl flex items-center justify-center mr-4 transition-all duration-300 ${
                        series.comingSoon ? '' : 'group-hover:scale-110'
                      }`}
                      style={{ 
                        background: series.comingSoon 
                          ? 'linear-gradient(135deg, #d1d5db, #9ca3af)'
                          : `linear-gradient(135deg, ${series.color}, ${series.color}dd)`,
                        boxShadow: series.comingSoon 
                          ? '0 4px 16px rgba(156, 163, 175, 0.3)'
                          : `0 4px 16px ${series.color}33`
                      }}
                    >
                      {series.comingSoon ? (
                        <svg className="w-8 h-8 text-white" fill="currentColor" viewBox="0 0 24 24">
                          <path d="M6 2v6h.01L6 8.01 10 12l-4 4 .01.01H6V22h12v-5.99h-.01L18 16l-4-4 4-3.99-.01-.01H18V2H6zm10 14.5V20H8v-3.5l4-4 4 4zm-4-5l-4-4V4h8v3.5l-4 4z"/>
                        </svg>
                      ) : (
                        <SeriesIcon type={series.icon} />
                      )}
                    </div>
                    <div className="flex-1">
                      <h3 
                        className="text-xl font-semibold mb-1"
                        style={{ 
                          color: series.comingSoon ? '#6b7280' : '#2c3e50',
                          letterSpacing: '0.02em',
                          fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif'
                        }}
                      >
                        {series.title}
                        {series.comingSoon && (
                          <span className="ml-2 text-sm font-normal text-amber-600">Em Breve</span>
                        )}
                      </h3>
                      <p 
                        className="text-sm"
                        style={{ color: series.comingSoon ? '#9ca3af' : 'rgba(127, 140, 141, 0.9)' }}
                      >
                        {series.description}
                      </p>
                    </div>
                  </div>

                  {/* Progress Section */}
                  {!series.comingSoon && (
                    <div className="space-y-4">
                      <div className="relative">
                        <div 
                          className="w-full rounded-full h-2 overflow-hidden"
                          style={{ backgroundColor: `${series.color}20` }}
                        >
                          <div
                            className="h-2 rounded-full transition-all duration-700 ease-out"
                            style={{ 
                              width: `${completionPercentage}%`,
                              background: completionPercentage > 0 ? 'linear-gradient(90deg, #3498db, #2980b9)' : `linear-gradient(90deg, ${series.color}, ${series.color}dd)`
                            }}
                          ></div>
                        </div>
                      </div>

                      <div className="flex justify-between items-center">
                        <div className="text-center">
                          <div 
                            className="text-lg font-semibold"
                            style={{ color: series.color }}
                          >
                            {progress.completedLessons}/{series.totalLessons}
                          </div>
                          <div 
                            className="text-xs"
                            style={{ color: 'rgba(127, 140, 141, 0.8)' }}
                          >
                            lies
                          </div>
                        </div>
                        <div className="text-center">
                          <div 
                            className="text-lg font-semibold"
                            style={{ color: '#2c3e50' }}
                          >
                            {completionPercentage}%
                          </div>
                          <div 
                            className="text-xs"
                            style={{ color: 'rgba(127, 140, 141, 0.8)' }}
                          >
                            completo
                          </div>
                        </div>
                        {progress.totalXP > 0 && (
                          <div className="text-center">
                            <div 
                              className="text-lg font-semibold"
                              style={{ color: '#3498db' }}
                            >
                              {progress.totalXP}
                            </div>
                            <div 
                              className="text-xs"
                              style={{ color: 'rgba(127, 140, 141, 0.8)' }}
                            >
                              XP total
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  )}

                  {/* Coming Soon Message */}
                  {series.comingSoon && (
                    <div className="text-center py-4">
                      <div className="text-amber-600 font-medium mb-2">
                         Contedo Avanado em Desenvolvimento
                      </div>
                      <div className="text-sm text-gray-500">
                        Aguarde novidades incrveis!
                      </div>
                    </div>
                  )}

                  {/* Action Area */}
                  <div className="flex items-center justify-between pt-2">
                    <span 
                      className="text-sm font-medium"
                      style={{ color: series.comingSoon ? '#9ca3af' : series.color }}
                    >
                      {series.comingSoon 
                        ? 'Aguarde o lanamento' 
                        : (completionPercentage === 0 ? 'Comear agora' : 
                           completionPercentage === 100 ? 'Revisar srie' : 'Continuar')
                      }
                    </span>
                    <svg 
                      className={`w-5 h-5 transition-all duration-300 ${
                        series.comingSoon ? '' : 'group-hover:translate-x-1'
                      }`}
                      style={{ color: series.comingSoon ? '#9ca3af' : 'rgba(127, 140, 141, 0.8)' }}
                      fill="none" 
                      stroke="currentColor" 
                      viewBox="0 0 24 24"
                    >
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                    </svg>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </>
  );
};

export default SeriesSelector;