import { 
  collection, 
  doc, 
  getDocs, 
  getDoc, 
  setDoc, 
  updateDoc, 
  query, 
  orderBy,
  where 
} from 'firebase/firestore';
import { db } from '../firebase';

// Get all students (for teacher dashboard)
export const getAllStudents = async () => {
  try {
    console.log(' Getting all students...');
    
    const studentsQuery = query(
      collection(db, 'students'), 
      orderBy('name')
    );
    
    const querySnapshot = await getDocs(studentsQuery);
    const students = [];
    
    querySnapshot.forEach((doc) => {
      students.push({
        id: doc.id,
        ...doc.data()
      });
    });
    
    console.log(` Found ${students.length} students`);
    return { success: true, students };
    
  } catch (error) {
    console.log(' Failed to get students:', error.message);
    return { success: false, error: error.message };
  }
};

// Get specific student's data and progress
export const getStudentData = async (username) => {
  try {
    console.log(' Getting student data for:', username);
    
    const studentDoc = await getDoc(doc(db, 'students', username));
    
    if (studentDoc.exists()) {
      const studentData = studentDoc.data();
      console.log(' Student data loaded');
      return { 
        success: true, 
        student: {
          id: studentDoc.id,
          ...studentData
        }
      };
    } else {
      throw new Error('Student not found');
    }
    
  } catch (error) {
    console.log(' Failed to get student data:', error.message);
    return { success: false, error: error.message };
  }
};

// FIXED: Update student's lesson progress - now matches StudentPortal call pattern
export const updateLessonProgress = async (username, lessonId, progressData) => {
  try {
    console.log(` Updating progress for ${username}: lesson ${lessonId}`);
    console.log(' Progress data:', progressData);
    
    // Update the student's progress using lessonId as the key
    const updatePath = `progress.${lessonId}`;
    
    await updateDoc(doc(db, 'students', username), {
      [updatePath]: progressData,
      lastActive: new Date()
    });
    
    console.log(' Progress updated successfully');
    return { success: true };
    
  } catch (error) {
    console.log(' Failed to update progress:', error.message);
    return { success: false, error: error.message };
  }
};

