// Get teacher's overview stats (for dashboard)
export const getTeacherStats = async () => {
  try {
    console.log(' Getting teacher stats...');
    
    const studentsResult = await getAllStudents();
    
    if (!studentsResult.success) {
      throw new Error(studentsResult.error);
    }
    
    const students = studentsResult.students;
    const totalStudents = students.length;
    
    // Count active students (active in last 7 days)
    const weekAgo = new Date(Date.now() - 7 * 24 * 60 * 60 * 1000);
    const activeStudents = students.filter(student => {
      const lastActive = student.lastActive?.toDate();
      return lastActive && lastActive > weekAgo;
    }).length;
    
    // Count total completed lessons across all students
    let totalCompletedLessons = 0;
    students.forEach(student => {
      if (student.progress) {
        totalCompletedLessons += Object.keys(student.progress).length;
      }
    });
    
    const stats = {
      totalStudents,
      activeStudents,
      totalCompletedLessons,
      totalSeries: 8, // Your 8 series
      totalLessons: 80 // Your 80 total lessons
    };
    
    console.log(' Teacher stats calculated:', stats);
    return { success: true, stats };
    
  } catch (error) {
    console.log(' Failed to get teacher stats:', error.message);
    return { success: false, error: error.message };
  }
};