                <svg className="w-4 h-4 mr-2 text-orange-500" fill="currentColor" viewBox="0 0 24 24">
                  <path d="M12 2c1.04 0 2.26.81 2.26 2.11 0 1.3-.98 2.12-2.26 2.12-1.28 0-2.26-.82-2.26-2.12C9.74 2.81 10.96 2 12 2zm-1.64 3.84c.13.12.3.2.49.2s.36-.08.49-.2c.13-.12.2-.28.2-.45s-.07-.33-.2-.45c-.13-.12-.3-.2-.49-.2s-.36.08-.49.2c-.13.12-.2.28-.2.45s.07.33.2.45zM5.68 9.1c.26-.31.71-.52 1.15-.52s.89.21 1.15.52c.26.31.31.72.13 1.06-.18.34-.56.56-.97.56s-.79-.22-.97-.56c-.18-.34-.13-.75.13-1.06zm8.64 0c.26-.31.71-.52 1.15-.52s.89.21 1.15.52c.26.31.31.72.13 1.06-.18.34-.56.56-.97.56s-.79-.22-.97-.56c-.18-.34-.13-.75.13-1.06zM12 16.5c-2.76 0-5-2.24-5-5s2.24-5 5-5 5 2.24 5 5-2.24 5-5 5zm0-8c-1.66 0-3 1.34-3 3s1.34 3 3 3 3-1.34 3-3-1.34-3-3-3z"/>
                </svg>
                <span 
