// src/services/auth.js
// This handles all login/logout operations for your app
// Think of it as your app's security guard

import { 
  signInWithEmailAndPassword, 
  signOut, 
  createUserWithEmailAndPassword,
  onAuthStateChanged 
} from 'firebase/auth';

import { doc, setDoc, getDoc } from 'firebase/firestore';
import { auth, db } from '../firebase';

// Login for TEACHER (uses email/password)
export const loginTeacher = async (email, password) => {
  try {
    console.log(' Trying to login teacher...');
    
    const userCredential = await signInWithEmailAndPassword(auth, email, password);
    const user = userCredential.user;
    
    // Check if teacher profile exists in Firestore
    const teacherDoc = await getDoc(doc(db, 'teachers', user.uid));
    
    if (teacherDoc.exists()) {
      // Profile exists, return it
      console.log(' Teacher login successful!');
      return {
        success: true,
        user: {
          uid: user.uid,
          email: user.email,
          role: 'teacher',
          ...teacherDoc.data()
        }
      };
    } else {
      // Profile doesn't exist, CREATE IT AUTOMATICALLY!
      console.log(' Creating teacher profile automatically...');
      
      const teacherData = {
        email: user.email,
        displayName: 'Teacher Alex', // You can change this later
        role: 'teacher',
        createdAt: new Date()
      };
      
      await setDoc(doc(db, 'teachers', user.uid), teacherData);
      
      console.log(' Teacher profile created and login successful!');
      return {
        success: true,
        user: {
          uid: user.uid,
          email: user.email,
          role: 'teacher',
          ...teacherData
        }
      };
    }
    
  } catch (error) {
    console.log(' Teacher login failed:', error.message);
    return {
      success: false,
      error: error.message
    };
  }
};

// Login for STUDENT (uses username/password - we'll store in students collection)
export const loginStudent = async (username, password) => {
  try {
    console.log(' Trying to login student...');
    
    // For students, we look them up in our students collection
    // Since Firebase Auth needs email, we'll store their email as username@teacher-alex.com
    const studentEmail = `${username}@teacher-alex.com`;
    
    const userCredential = await signInWithEmailAndPassword(auth, studentEmail, password);
    const user = userCredential.user;
    
    // Get student data from our students collection
    const studentDoc = await getDoc(doc(db, 'students', username));
    
    if (studentDoc.exists()) {
      console.log(' Student login successful!');
      return {
        success: true,
        user: {
          uid: user.uid,
          username: username,
          role: 'student',
          ...studentDoc.data()
        }
      };
    } else {
      throw new Error('Student account not found.');
    }
    
  } catch (error) {
    console.log(' Student login failed:', error.message);
    return {
      success: false,
      error: error.message
    };
  }
};

// Create a new STUDENT account (only teachers can do this)
export const createStudentAccount = async (studentData) => {
  try {
    const { username, password, name, series } = studentData;
    
    console.log(' Creating student account for:', name);
    
    // Create email for Firebase Auth (students don't need real emails)
    const studentEmail = `${username}@teacher-alex.com`;
    
    // Create the authentication account
    const userCredential = await createUserWithEmailAndPassword(auth, studentEmail, password);
    const user = userCredential.user;
    
    // Store student data in Firestore
    await setDoc(doc(db, 'students', username), {
      name: name,
      username: username,
      email: studentEmail,
      uid: user.uid,
      series: series, // Which lesson series they're assigned to
      createdAt: new Date(),
      progress: {}, // Will track lesson progress here
      lastActive: new Date()
    });
    
    // Sign out the newly created account (so teacher stays logged in)
    await signOut(auth);
    
    console.log(' Student account created successfully!');
    return { 
      success: true, 
      message: `Student ${name} created successfully!` 
    };
    
  } catch (error) {
    console.log(' Failed to create student:', error.message);
    return { 
      success: false, 
      error: error.message 
    };
  }
};

// Logout (works for both teachers and students)
export const logout = async () => {
  try {
    await signOut(auth);
    console.log(' User logged out successfully');
    return { success: true };
  } catch (error) {
    console.log(' Logout failed:', error.message);
    return { 
      success: false, 
      error: error.message 
    };
  }
};

// Listen for authentication state changes
// This tells us when someone logs in or out
export const onAuthChange = (callback) => {
  return onAuthStateChanged(auth, callback);
};