// src/components/TeacherDashboard.jsx
// Command center - CLEAN PROFESSIONAL PALETTE (Azul + Vermelho + Cinzas)

import React, { useState, useEffect } from 'react';
import { createStudentAccount } from '../services/auth';
import { getAllStudents, getTeacherStats } from '../services/database';

const TeacherDashboard = ({ user, onLogout }) => {
  // State for different sections
  const [activeTab, setActiveTab] = useState('overview');
  const [students, setStudents] = useState([]);
  const [stats, setStats] = useState({});
  const [loading, setLoading] = useState(true);

  // State for creating new students
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [newStudent, setNewStudent] = useState({
    name: '',
    username: '',
    password: '',
    series: 'basic-english'
  });
  const [createLoading, setCreateLoading] = useState(false);
  const [message, setMessage] = useState({ type: '', text: '' });

  // Available lesson series
  const seriesOptions = [
    { value: 'basic-english', label: ' Basic English' },
    { value: 'intermediate-english', label: ' Intermediate English' },
    { value: 'business-english', label: ' Business English' },
    { value: 'conversation-english', label: ' Conversation English' },
    { value: 'travel-english', label: ' Travel English' },
    { value: 'academic-english', label: ' Academic English' },
    { value: 'pronunciation-english', label: ' Pronunciation English' },
    { value: 'grammar-english', label: ' Grammar English' }
  ];

  // Load data when component mounts
  useEffect(() => {
    loadDashboardData();
  }, []);

  const loadDashboardData = async () => {
    setLoading(true);
    try {
      // Load students and stats
      const [studentsResult, statsResult] = await Promise.all([
        getAllStudents(),
        getTeacherStats()
      ]);

      if (studentsResult.success) {
        setStudents(studentsResult.students);
      }

      if (statsResult.success) {
        setStats(statsResult.stats);
      }
    } catch (error) {
      console.error('Error loading dashboard data:', error);
      setMessage({ type: 'error', text: 'Failed to load dashboard data' });
    }
    setLoading(false);
  };

  // Handle creating new student
  const handleCreateStudent = async (e) => {
    e.preventDefault();
    setCreateLoading(true);
    setMessage({ type: '', text: '' });

    try {
      const result = await createStudentAccount(newStudent);
      
      if (result.success) {
        setMessage({ type: 'success', text: result.message });
        setNewStudent({ name: '', username: '', password: '', series: 'basic-english' });
        setShowCreateForm(false);
        // Reload students list
        loadDashboardData();
      } else {
        setMessage({ type: 'error', text: result.error });
      }
    } catch (error) {
      setMessage({ type: 'error', text: 'Failed to create student account' });
    }
    setCreateLoading(false);
  };

  // Handle input changes for new student form
  const handleInputChange = (e) => {
    setNewStudent({
      ...newStudent,
      [e.target.name]: e.target.value
    });
  };

  if (loading) {
    return (
      <div 
        className="min-h-screen flex items-center justify-center"
        style={{ backgroundColor: '#ecf0f1' }}
      >
        <div className="text-center">
          <div 
            className="animate-spin rounded-full h-12 w-12 border-b-2 mx-auto mb-4"
            style={{ borderBottomColor: '#196de3' }}
          ></div>
          <p style={{ color: '#7f8c8d' }}>Loading your dashboard...</p>
        </div>
      </div>
    );
  }

  return (
    <div 
      className="min-h-screen"
      style={{ backgroundColor: '#ecf0f1' }}
    >
      {/* Header */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center py-4">
            <h1 
              className="text-xl font-bold"
              style={{ color: '#2c3e50' }}
            >
               Teacher Alex's English Academy
            </h1>
            <div className="flex items-center space-x-4">
              <span 
                className="text-sm" 
                style={{ color: '#7f8c8d' }}
              >
                Welcome, {user.displayName}!
              </span>
              <button
                onClick={onLogout}
                className="text-white px-4 py-2 rounded-lg transition-colors"
                style={{ backgroundColor: '#bf3d30' }}
                onMouseEnter={(e) => e.target.style.filter = 'brightness(0.9)'}
                onMouseLeave={(e) => e.target.style.filter = 'brightness(1)'}
              >
                Logout
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        
        {/* Tab Navigation */}
        <div className="mb-8">
          <nav className="flex space-x-8">
            <button
              onClick={() => setActiveTab('overview')}
              className={`py-2 px-1 border-b-2 font-medium text-sm transition-colors`}
              style={{
                borderBottomColor: activeTab === 'overview' ? '#196de3' : 'transparent',
                color: activeTab === 'overview' ? '#196de3' : '#7f8c8d'
              }}
              onMouseEnter={(e) => {
                if (activeTab !== 'overview') e.target.style.color = '#2c3e50';
              }}
              onMouseLeave={(e) => {
                if (activeTab !== 'overview') e.target.style.color = '#7f8c8d';
              }}
            >
               Overview
            </button>
            <button
              onClick={() => setActiveTab('students')}
              className={`py-2 px-1 border-b-2 font-medium text-sm transition-colors`}
              style={{
                borderBottomColor: activeTab === 'students' ? '#196de3' : 'transparent',
                color: activeTab === 'students' ? '#196de3' : '#7f8c8d'
              }}
              onMouseEnter={(e) => {
                if (activeTab !== 'students') e.target.style.color = '#2c3e50';
              }}
              onMouseLeave={(e) => {
                if (activeTab !== 'students') e.target.style.color = '#7f8c8d';
              }}
            >
               Students ({students.length})
            </button>
            <button
              onClick={() => setActiveTab('create')}
              className={`py-2 px-1 border-b-2 font-medium text-sm transition-colors`}
              style={{
                borderBottomColor: activeTab === 'create' ? '#196de3' : 'transparent',
                color: activeTab === 'create' ? '#196de3' : '#7f8c8d'
              }}
              onMouseEnter={(e) => {
                if (activeTab !== 'create') e.target.style.color = '#2c3e50';
              }}
              onMouseLeave={(e) => {
                if (activeTab !== 'create') e.target.style.color = '#7f8c8d';
              }}
            >
               Add Student
            </button>
          </nav>
        </div>

        {/* Message Display */}
        {message.text && (
          <div 
            className={`mb-6 p-4 rounded-lg border`}
            style={{
              backgroundColor: message.type === 'success' ? '#d4edda' : '#f8d7da',
              borderColor: message.type === 'success' ? '#c3e6cb' : '#f5c6cb',
              color: message.type === 'success' ? '#155724' : '#721c24'
            }}
          >
            {message.type === 'success' ? '' : ''} {message.text}
          </div>
        )}

        {/* Overview Tab */}
        {activeTab === 'overview' && (
          <div className="space-y-6">
            <div className="bg-white rounded-lg shadow p-6">
              <h2 
                className="text-2xl font-bold mb-4"
                style={{ color: '#2c3e50' }}
              >
                 Academy Overview
              </h2>
              
              {/* Stats Cards - Clean Professional Colors */}
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4 mb-6">
                <div 
                  className="border rounded-lg p-4"
                  style={{ 
                    backgroundColor: 'rgba(25, 109, 227, 0.1)', 
                    borderColor: 'rgba(25, 109, 227, 0.3)' 
                  }}
                >
                  <div 
                    className="text-2xl font-bold"
                    style={{ color: '#196de3' }}
                  >
                    {stats.totalStudents || 0}
                  </div>
                  <div 
                    className="text-sm"
                    style={{ color: '#2c3e50' }}
                  >
                    Total Students
                  </div>
                </div>
                <div 
                  className="border rounded-lg p-4"
                  style={{ 
                    backgroundColor: 'rgba(191, 61, 48, 0.1)', 
                    borderColor: 'rgba(191, 61, 48, 0.3)' 
                  }}
                >
                  <div 
                    className="text-2xl font-bold"
                    style={{ color: '#bf3d30' }}
                  >
                    {stats.activeStudents || 0}
                  </div>
                  <div 
                    className="text-sm"
                    style={{ color: '#2c3e50' }}
                  >
                    Active This Week
                  </div>
                </div>
                <div 
                  className="border rounded-lg p-4"
                  style={{ 
                    backgroundColor: 'rgba(127, 140, 141, 0.1)', 
                    borderColor: 'rgba(127, 140, 141, 0.3)' 
                  }}
                >
                  <div 
                    className="text-2xl font-bold"
                    style={{ color: '#7f8c8d' }}
                  >
                    {stats.totalCompletedLessons || 0}
                  </div>
                  <div 
                    className="text-sm"
                    style={{ color: '#2c3e50' }}
                  >
                    Lessons Completed
                  </div>
                </div>
                <div 
                  className="border rounded-lg p-4"
                  style={{ 
                    backgroundColor: 'rgba(44, 62, 80, 0.1)', 
                    borderColor: 'rgba(44, 62, 80, 0.3)' 
                  }}
                >
                  <div 
                    className="text-2xl font-bold"
                    style={{ color: '#2c3e50' }}
                  >
                    {stats.totalSeries || 8}
                  </div>
                  <div 
                    className="text-sm"
                    style={{ color: '#7f8c8d' }}
                  >
                    Lesson Series
                  </div>
                </div>
              </div>

              <div className="border-t pt-4">
                <h3 
                  className="font-semibold mb-2"
                  style={{ color: '#2c3e50' }}
                >
                   Quick Actions
                </h3>
                <div className="flex flex-wrap gap-2">
                  <button
                    onClick={() => setActiveTab('create')}
                    className="text-white px-4 py-2 rounded-lg transition-colors"
                    style={{ backgroundColor: '#196de3' }}
                    onMouseEnter={(e) => e.target.style.filter = 'brightness(0.9)'}
                    onMouseLeave={(e) => e.target.style.filter = 'brightness(1)'}
                  >
                     Add New Student
                  </button>
                  <button
                    onClick={() => setActiveTab('students')}
                    className="text-white px-4 py-2 rounded-lg transition-colors"
                    style={{ backgroundColor: '#bf3d30' }}
                    onMouseEnter={(e) => e.target.style.filter = 'brightness(0.9)'}
                    onMouseLeave={(e) => e.target.style.filter = 'brightness(1)'}
                  >
                     View All Students
                  </button>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Students Tab */}
        {activeTab === 'students' && (
          <div className="bg-white rounded-lg shadow">
            <div className="px-6 py-4 border-b">
              <h2 
                className="text-xl font-bold"
                style={{ color: '#2c3e50' }}
              >
                 Your Students
              </h2>
            </div>
            
            {students.length === 0 ? (
              <div className="p-8 text-center">
                <div className="text-4xl mb-4"></div>
                <h3 
                  className="text-lg font-medium mb-2"
                  style={{ color: '#2c3e50' }}
                >
                  No students yet
                </h3>
                <p 
                  className="mb-4"
                  style={{ color: '#7f8c8d' }}
                >
                  Start by creating your first student account!
                </p>
                <button
                  onClick={() => setActiveTab('create')}
                  className="text-white px-6 py-2 rounded-lg transition-colors"
                  style={{ backgroundColor: '#196de3' }}
                  onMouseEnter={(e) => e.target.style.filter = 'brightness(0.9)'}
                  onMouseLeave={(e) => e.target.style.filter = 'brightness(1)'}
                >
                   Create First Student
                </button>
              </div>
            ) : (
              <div className="overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200">
                  <thead className="bg-gray-50">
                    <tr>
                      <th 
                        className="px-6 py-3 text-left text-xs font-medium uppercase tracking-wider"
                        style={{ color: '#7f8c8d' }}
                      >
                        Student
                      </th>
                      <th 
                        className="px-6 py-3 text-left text-xs font-medium uppercase tracking-wider"
                        style={{ color: '#7f8c8d' }}
                      >
                        Series
                      </th>
                      <th 
                        className="px-6 py-3 text-left text-xs font-medium uppercase tracking-wider"
                        style={{ color: '#7f8c8d' }}
                      >
                        Progress
                      </th>
                      <th 
                        className="px-6 py-3 text-left text-xs font-medium uppercase tracking-wider"
                        style={{ color: '#7f8c8d' }}
                      >
                        Last Active
                      </th>
                    </tr>
                  </thead>
                  <tbody className="bg-white divide-y divide-gray-200">
                    {students.map((student) => (
                      <tr key={student.id} className="hover:bg-gray-50">
                        <td className="px-6 py-4 whitespace-nowrap">
                          <div className="flex items-center">
                            <div 
                              className="text-sm font-medium"
                              style={{ color: '#2c3e50' }}
                            >
                              {student.name}
                            </div>
                            <div 
                              className="text-sm ml-2"
                              style={{ color: '#7f8c8d' }}
                            >
                              (@{student.username})
                            </div>
                          </div>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <span 
                            className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium"
                            style={{ 
                              backgroundColor: 'rgba(25, 109, 227, 0.1)', 
                              color: '#196de3' 
                            }}
                          >
                            {student.series}
                          </span>
                        </td>
                        <td className="px-6 py-4 whitespace-nowrap">
                          <div 
                            className="text-sm"
                            style={{ color: '#2c3e50' }}
                          >
                            {student.progress ? Object.keys(student.progress).length : 0} lessons completed
                          </div>
                        </td>
                        <td 
                          className="px-6 py-4 whitespace-nowrap text-sm"
                          style={{ color: '#7f8c8d' }}
                        >
                          {student.lastActive ? 
                            new Date(student.lastActive.seconds * 1000).toLocaleDateString() : 
                            'Never'
                          }
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            )}
          </div>
        )}

        {/* Create Student Tab */}
        {activeTab === 'create' && (
          <div className="bg-white rounded-lg shadow p-6">
            <h2 
              className="text-xl font-bold mb-6"
              style={{ color: '#2c3e50' }}
            >
               Create New Student Account
            </h2>
            
            <form onSubmit={handleCreateStudent} className="space-y-4 max-w-md">
              <div>
                <label 
                  className="block text-sm font-medium mb-2"
                  style={{ color: '#2c3e50' }}
                >
                  Student Name
                </label>
                <input
                  type="text"
                  name="name"
                  value={newStudent.name}
                  onChange={handleInputChange}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:border-transparent"
                  style={{ '--tw-ring-color': '#196de3' }}
                  placeholder="e.g., Joo Silva"
                  required
                />
              </div>

              <div>
                <label 
                  className="block text-sm font-medium mb-2"
                  style={{ color: '#2c3e50' }}
                >
                  Username (for login)
                </label>
                <input
                  type="text"
                  name="username"
                  value={newStudent.username}
                  onChange={handleInputChange}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:border-transparent"
                  placeholder="e.g., joao"
                  required
                />
                <p 
                  className="text-xs mt-1"
                  style={{ color: '#7f8c8d' }}
                >
                  This will be used for student login (letters and numbers only)
                </p>
              </div>

              <div>
                <label 
                  className="block text-sm font-medium mb-2"
                  style={{ color: '#2c3e50' }}
                >
                  Password
                </label>
                <input
                  type="password"
                  name="password"
                  value={newStudent.password}
                  onChange={handleInputChange}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:border-transparent"
                  placeholder="Create a secure password"
                  required
                />
              </div>

              <div>
                <label 
                  className="block text-sm font-medium mb-2"
                  style={{ color: '#2c3e50' }}
                >
                  Lesson Series
                </label>
                <select
                  name="series"
                  value={newStudent.series}
                  onChange={handleInputChange}
                  className="w-full px-4 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:border-transparent"
                >
                  {seriesOptions.map(option => (
                    <option key={option.value} value={option.value}>
                      {option.label}
                    </option>
                  ))}
                </select>
                <p 
                  className="text-xs mt-1"
                  style={{ color: '#7f8c8d' }}
                >
                  Choose which lesson series this student will follow
                </p>
              </div>

              <button
                type="submit"
                disabled={createLoading}
                className={`w-full text-white py-3 px-4 rounded-lg transition-colors ${
                  createLoading ? 'opacity-50 cursor-not-allowed' : ''
                }`}
                style={{ backgroundColor: '#196de3' }}
                onMouseEnter={(e) => {
                  if (!createLoading) e.target.style.filter = 'brightness(0.9)';
                }}
                onMouseLeave={(e) => {
                  if (!createLoading) e.target.style.filter = 'brightness(1)';
                }}
              >
                {createLoading ? (
                  <div className="flex items-center justify-center">
                    <div className="animate-spin rounded-full h-5 w-5 border-b-2 border-white mr-2"></div>
                    Creating Account...
                  </div>
                ) : (
                  ' Create Student Account'
                )}
              </button>
            </form>
          </div>
        )}

      </div>
    </div>
  );
};

export default TeacherDashboard;